	module.exports = {"data":{"site":{"siteMetadata":{"title":"Solar Sailer","description":"I am a developer, designer and product manager from France. Co-founder of Pixelnest Studio.","author":"Matthieu Oger","siteUrl":"https://solarsailer.net/"}},"markdownRemark":{"html":"<p><a href=\"http://arstechnica.com/tech-policy/2013/09/angry-entrepreneur-replies-to-patent-troll-with-racketeering-lawsuit/\">Angry entrepreneur replies to patent troll with racketeering lawsuit</a>:</p>\n<blockquote>\n<p>\"There's a lot of outrageous stories, but everyone's so damn afraid of coming forward—It's like going against the Mafia,\" he said. But the idea that trolls may retaliate against those who speak out is overblown, he thinks. \"If they want to try to teach me a lesson, go for it. This will be my retirement. I'll fight them.\"</p>\n</blockquote>\n<p>Here it is:</p>\n<ul>\n<li>Everyone knows that patent trolls are useless companies that only try to steal money from defenseless ones.</li>\n<li>Everyone knows that they don't invent anything.</li>\n<li>Everyone knows that they cost a lot — of time and money — with endless legal procedures.</li>\n<li>Everyone knows that it's pure bullshit and extortion, and yet, it is perfectly legal.</li>\n</ul>\n<p>Stupid system.</p>\n<hr>\n<p>I'm not against patents and inventors' rights. But there is a big difference between protecting your properties from real thieves and suing everyone without actually creating anything.</p>\n<p>However, if the only solution to this problem is to abolish the patents system, so be it.</p>","frontmatter":{"date":"2013-09-17","readableDate":"17 September 2013","title":"Patent trolls","showPageTitle":null,"excerptLine":"I'm not against patents and inventors' rights. But there is a big difference between protecting your properties from real thieves and suing everyone without actually creating anything. If the only solution to this problem is to abolish the patents system, so be it.","excerptQuote":null},"fields":{"path":"/2013/09/patent-trolls/"}}},"pathContext":{"id":"/Users/ashen/Code/solarsailer.net/content/posts/2013-09-17-patent-trolls.md absPath of file >>> MarkdownRemark"}}

